.wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    background-color: #ee8375;
    padding: 20px;
}

.icon {
    width: 60px;
    height: 60px;
    border-radius: 10px;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    position: relative;
    text-decoration: none;
    transition: transform 0.3s ease;
}

.icon::before {
    content: '';
    position: absolute;
    width: 130%;
    height: 130%;
    left: -110%;
    top: 80%;
    transform: rotate(45deg);
    transition: all 0.3s ease;
    z-index: 0;
}

.icon:hover::before {
    animation: slide 0.7s forwards;
}

@keyframes slide {
    0% {
        left: -110%;
        top: 80%;
    }

    50% {
        left: 10%;
        top: -40%;
    }

    100% {
        left: -15%;
        top: -15%;
    }
}

.iconSvg {
    position: relative;
    z-index: 1;
    color: #000;
    transition: transform 0.3s ease;
    font-size: 20px;
}

.icon:hover .iconSvg {
    transform: scale(1.2);
    color: #f1f1f1;
}


.facebook::before {
    background-color: #3b5999;
}

.twitter::before {
    background-color: #55acee;
}

.linkedin::before {
    background-color: #0077b5;
}

.google::before {
    background-color: #f56856;
}

.youtube::before {
    background-color: #ef4028;
}

.telegram::before {
    background-color: #168ac8;
}

.instagram::before {
    background-color: #e1306c;
}

.whatsapp::before {
    background-color: #f05908;
}

.tiktok::before {
    background-color: #69c9d0;
}

.slack::before {
    background-color: #4a154b;
}

.discord::before {
    background-color: #5865f2;
}


@media (min-width: 768px) {
    .wrapper {
        gap: 1.5rem;
        padding: 40px;
    }

    .icon {
        width: 70px;
        height: 70px;
        border-radius: 12px;
    }

    .iconSvg {
        font-size: 24px;
    }
}


@media (min-width: 1200px) {
    .wrapper {
        gap: 2rem;
        padding: 56px;
    }

    .icon {
        width: 90px;
        height: 90px;
        border-radius: 14px;
    }

    .iconSvg {
        font-size: 28px;
    }
}